import {CST} from "../CST";

export class MenuScene extends Phaser.Scene {
	constructor(){
		super({
			key: CST.SCENES.MENU
		})
	}


	init(data){
		console.log(data);
		console.log("MENU");
	}

	preload(){

	}

	create(){
		this.add.image(0,0,"title_bg").setOrigin(0);
		this.createButtons();
	}



  	createButtons(){
       
      let y = 250;
      let style = { font: "bold 45px Georgia", fill: "#1b2fc6", boundsAlignH: "center", boundsAlignV: "middle" };
        
        
       const startButton = this.add.text(100, y, 'New Game', style)
      .setInteractive()
      .on('pointerover', () => this.enterButtonHoverState(startButton) )
      .on('pointerout', () => this.enterButtonRestState(startButton) )
      .on('pointerdown', () => console.log("starting") );
      
      y += 150;
      
       const highScoreButton = this.add.text(100, y, 'High Score', style)
      .setInteractive()
      .on('pointerover', () => this.enterButtonHoverState(highScoreButton) )
      .on('pointerout', () => this.enterButtonRestState(highScoreButton) )
      
       y += 150;
         const kkt = 5;
      
       const exitButton = this.add.text(100, y, 'Exit ', style)
      .setInteractive()
      .on('pointerover', () => this.enterButtonHoverState(exitButton) )
      .on('pointerout', () => this.enterButtonRestState(exitButton) )
      
      
    }

   enterButtonHoverState(button){
        button.setStyle({ fill: '#4b5de5'});
    }
    
    enterButtonRestState(button) {
        button.setStyle({ fill: '#1b2fc6' });
  	}
  
  // startNewGame(){
  //     this.scene.switch('gameScene');
  // } 
}